<template>
  <div id="app">
      <transition :name="pageChangeAnimation">
        <keep-alive>
          <router-view transition-mode="out-in" :class="transitionClass"></router-view>
        </keep-alive>
      </transition>
      
  </div>
</template>

<script>
    import StateHelper from '@/animation/state-helper';

    export default {
      name: 'App',
      data(){
          return {
              transitionClass:'vue-page-animation-router-view-vertical',
              pageChangeAnimation: 'slide-horizontal'
          }
      },
      created() {
        this.stateHelper = new StateHelper(false);
      },
      watch: {
        $route(to, from) {
          const stateHelper = this.stateHelper;
          stateHelper.update();
          
          if(to.meta.index != from.meta.index){
            if (stateHelper.isPageBack()) {
                this.transitionClass = 'vue-page-animation-router-view-vertical-end';
                this.pageChangeAnimation = 'slide-vertical-end';
                this.Global.subPageCount = 0;
            } else {
                if (to.meta.index == 1 && from.meta.index == 2) {
                    this.transitionClass = 'vue-page-animation-router-view-vertical-end';
                    this.pageChangeAnimation = 'slide-vertical-end';
                    this.Global.subPageCount = 0;
                } else {
                    this.transitionClass = 'vue-page-animation-router-view-vertical-start';
                    this.pageChangeAnimation = 'slide-vertical-start';
                    this.Global.subPageCount = 1;
                }
            }
          } else {
            this.transitionClass = 'vue-page-animation-router-view-horizontal';
            if (stateHelper.isPageBack()) {
               this.pageChangeAnimation = 'slide-horizontal-right';
               if (to.meta.index == 2) {
                   this.Global.subPageCount--;
               }
            } else {
               this.pageChangeAnimation = 'slide-horizontal-left';
               if (to.meta.index == 2) {
                   this.Global.subPageCount++;
               }
            }
          }
        }
      },
    }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
}

.vue-page-animation-router-view-horizontal {
  width: 100%;
  position: absolute;
  left: 0;
  will-change: left, opacity;
  -webkit-transition-duration: .3s;
  transition-duration: .3s;
  -webkit-transition-property: left, opacity;
  transition-property: left, opacity;
  -webkit-transition-timing-function: cubic-bezier(.55,0,.1,1);
          transition-timing-function: cubic-bezier(.55,0,.1,1);
}
.vue-page-animation-router-view-vertical-start {
  width: 100%;
  position: absolute;
  top: 0;
  will-change: top, opacity;
  -webkit-transition-duration: .3s;
  transition-duration: .3s;
  -webkit-transition-property: top, opacity;
  transition-property: top, opacity;
  -webkit-transition-timing-function: cubic-bezier(.55,0,.1,1);
          transition-timing-function: cubic-bezier(.55,0,.1,1);
}

.vue-page-animation-router-view-vertical-end {
  width: 100%;
  position: absolute;
  top: 0;
  will-change: top, opacity;
  -webkit-transition-duration: .3s;
  transition-duration: .3s;
  -webkit-transition-property: top, opacity;
  transition-property: top, opacity;
  -webkit-transition-timing-function: cubic-bezier(.55,0,.1,1);
          transition-timing-function: cubic-bezier(.55,0,.1,1);
}

.slide-vertical-start-enter-active {
  transition: all .3s ease;
}
.slide-vertical-start-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-vertical-start-enter  {
  transform: translateY(100%);
  opacity: 0;
}
.slide-vertical-start-leave-to  {
  /*transform: translateY(100%);*/
  opacity: 0;
}

.slide-vertical-end-enter-active {
  transition: all .5s ease;
}
.slide-vertical-end-leave-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-vertical-end-enter  {
  /*transform: translateY(100%);*/
  opacity: 0;
}
.slide-vertical-end-leave-to  {
  transform: translateY(100%);
  opacity: 0;
}

.slide-horizontal-right-enter-active {
  transition: all .3s ease;
}
.slide-horizontal-right-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-horizontal-right-enter  {
  transform: translateX(-100%);
