<template>
  <div class="page"  >
      <header class="app-header">
        <h2>vue-pull-to-examples</h2>
      </header>
      <div style="margin-top:50px;">
        <pull-to :top-load-method="refresh">
          <ul class="article-list">
              <li class="article-item" v-for="(item, index) in dataList">
                  <div class="article-info">
                      <p class="article-title">{{ item }}</p>
                  </div>
              </li>
          </ul>
        </pull-to>
      </div>
  </div>
</template>

<script>
  import PullTo from 'vue-pull-to'
  
  export default {
    name: 'example',
    components: {
      PullTo
    },
    data() {
      return {
        dataList: ["item01","item02","item03","item04","item05","item06","item07","item08","item09","item10","item11","item12","item13","item14","item15","item16"],
        containerHeight: 0
      }
    },
    mounted: function() {
//      for (var i = 0; i < 20; i++) {
//          this.dataList.push('item' + i)
//      }
        this.containerHeight = window.innerHeight;
        debugger
    },
    methods: {
      refresh(loaded) {
        setTimeout(() => {
          this.dataList.reverse();
          loaded('done');
        }, 2000);
      }
    }
  }
</script>

<style scoped>
    .page {
        margin-top: 0px;
    }
    .article-item {
       display: flex;
       padding:3px;
       height: 100px;
       background: #fff;
       border-bottom: 1px solid #7D7D7D;
       overflow: hidden;
       cursor: pointer;
   }
   ul {
       margin: 0;
       padding: 0;
   }
   li {
        margin: 0;
        padding: 0;
        list-style: none;
   }
   .article-info {
       width: calc(100% - 141px);
   }
   .article-title {
       width: 100%;
       height: 38px;
       font-size: 16px;
       color: #2B2B2B;
       word-wrap: break-word;
       font-weight: 700;
       line-height: 19px;
       display: -webkit-box;
       overflow: hidden;
       text-overflow: ellipsis;
       -webkit-box-orient: vertical;
       -webkit-line-clamp: 2;
       margin: 5px 0px 15px 0px;
    }
    .app-header {
      position: absolute;
      left: 0;
      top: 0;
      box-sizing: border-box;
      align-items: center;
      width: 100%;
      height: 50px;
      z-index: 500;
      background: #35495e;
      color: #fff;
    }
    h2 {
      font-size: 20px;
      font-weight: 300;
      line-height: 50px;
      text-align: center;
    }
</style>